@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Noto Sans KR';

}
.App {

  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.main{
    display: grid;
    grid-template-columns: 5% 75% 20%;
    width: 100%;
    
    background-color: #333232
}
.left_container,.middle_container, .right_container{
    
    border-right: 1px solid white;
}

.dashboard_title{
    height: 6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin-left: 1rem;
}

.left_container{
   
    display: -webkit-flex;
   
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: white;
}
.left_subcontainer{
    margin-top: 5rem;
}

.div2{
    background: #00bcd4;
    border: none;
}

.dashboard_title_right{
    height: 6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

}

.each_box{
    width: 90%;
 
    padding: 1rem 0;
    cursor: pointer;
}

.each_box:hover{
    color: tomato;
    border-right: .5rem solid white;
    border-radius: 10px;
}

.first_container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(17rem, 1fr));
    margin: 0 1rem;
    grid-gap: 2rem;
    gap: 2rem;

}

.div2, .div1 {
    
    color: white;
    border-radius: 10px;
    
    border: 1px solid rgb(146, 146, 146);
}

.div_header{

} 
.div_subcontainer{
    display: -webkit-flex;
    display: flex;
}

.div2{
    border:none
}

.div_subcontainer div{
    width: 50%;
}
.div_header{
    display: -webkit-flex;
    display: flex;
    margin-left: 2rem;
}
.div_header p{
    font-size: .9rem;
}

.arrowIcon{
    color: turquoise;
    margin-right: .5rem;
    font-size: .8rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.arrowDown{
    font-size: .8rem;
    color: tomato;
    margin-right: .5rem;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.percentageDown{
    font-size: .8rem;
    color: tomato;
}

.percentageUp{
    font-size: .8rem;
    color: turquoise;
}

.second_container {
    display: grid;
    grid-template-columns: 2fr 1fr;
    margin: 1rem;
    grid-gap: 2rem;
    gap: 2rem;

    padding: 1rem 0;
}
.second_container .left, .right{
    border: 1px solid rgb(146, 146, 146);
    border-radius: 10px;
}

.second_container .right{
    color: white;
}
.second_container .right p{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-left: 1.5rem;
}

.bigDiv{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.progress{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.div2{
    -webkit-transform: translate(1rem,1rem);
            transform: translate(1rem,1rem);
    
}

.new_applicants p{
    color: white;
}

.new_applicants{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.each_applicant{
    display:-webkit-flex;
    display:flex;
    width:90%;
    margin-left:1rem
}

.applicant_icon{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.message_icon, .call_icon{
    color: white;
    cursor: pointer;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

